export default ".calculator {\r\n  width: 320px;\r\n  padding: 30px;\r\n  border: 2px solid #ccc;\r\n  border-radius: 10px;\r\n  background-color: #f9f9f9;\r\n  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);\r\n  padding-right: 4%;\r\n}\r\n\r\n.calculator-header {\r\n  text-align: center;\r\n  font-size: 28px;\r\n  color: #333;\r\n  margin-bottom: 15px;\r\n  font-weight: bold;\r\n}\r\n\r\n.display {\r\n  width: 100%;\r\n  font-size: 24px;\r\n  padding: 10px;\r\n  margin-bottom: 10px;\r\n  text-align: right;\r\n  border: 1px solid #ccc;\r\n  border-radius: 8px;\r\n  background-color: #fff;\r\n  box-shadow: inset 0px 0px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.buttons {\r\n  display: grid;\r\n  grid-template-columns: repeat(4, 1fr);\r\n  gap: 10px;\r\n}\r\n\r\n.btn {\r\n  width: 100%;\r\n  padding: 10px;\r\n  font-size: 18px;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  background-color: #e0e0e0;\r\n  transition: background-color 0.2s, transform 0.1s;\r\n}\r\n\r\n.btn:hover {\r\n  background-color: #ccc;\r\n  transform: scale(1.05);\r\n}\r\n\r\n.btn-0,\r\n.btn-1,\r\n.btn-2,\r\n.btn-3,\r\n.btn-4,\r\n.btn-5,\r\n.btn-6,\r\n.btn-7,\r\n.btn-8,\r\n.btn-9 {\r\n  background-color: #bdc3c7;\r\n  color: #2c3e50;\r\n}\r\n\r\n.btn-divide {\r\n  background-color: #f1c40f;\r\n  color: #fff;\r\n}\r\n.btn-multiply {\r\n  background-color: #27ae60;\r\n  color: #191818;\r\n}\r\n.btn-minus {\r\n  background-color: #e67e22;\r\n  color: #fff;\r\n}\r\n.btn-plus {\r\n  background-color: #db3466;\r\n  color: #fff;\r\n}\r\n.btn-equal {\r\n  background-color: #2980b9;\r\n  color: #fff;\r\n}\r\n.btn-C {\r\n  background-color: #c0392b;\r\n  color: #fff;\r\n}\r\n.btn-dot {\r\n  background-color: #7f8c8d;\r\n  color: #fff;\r\n}\r\n\r\n.expression {\r\n  font-size: 16px;\r\n  text-align: right;\r\n  margin-top: 10px;\r\n  color: #777;\r\n}\r\n";